.bottom-menu{
  position: absolute;
  left: 0;
  bottom: 0;
  display: flex;
  width: 100%;
  height: 16.67vw;
  background: #fff;
  box-shadow: 0 0.56vw 1.39vw rgba(0, 0, 0, 0.3);
  z-index: 10;

  li {
    display: flex;
    justify-content: center;
    flex: 1;

    a {
      position: relative;
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100%;
      justify-content: center;
      align-items: center;
    }

    img {
      width: 6.67vw;
      height: 6.67vw;
    }

    p {
      margin-top: 1.39vw;
      text-align: center;
      font-size: 3.33vw;
      color: #999;
    }
  }
}